import styled from "styled-components";

import { colors } from "../styleguide";
import { marginsCss } from "../styleguide/marginInterface";

import { StyledCardProps } from "./Card.types";

export const StyledCard = styled.div<StyledCardProps>`
	display: flex;
	flex-direction: column;
	box-sizing: border-box;
	overflow: auto;
	width: 100%;
	padding: ${({ padding }) => padding};
	box-shadow: 0 2px 8px -2px ${colors.black}1A, 0 0 1px ${colors.black}52;
	border-radius: 16px;
	${marginsCss}

	background:  ${({ isInverted }) => isInverted ? colors.black : colors.white};

	${({ isClickable }) => isClickable && `
		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px ${colors.white}, 0 0 0 4px ${colors.midBlue};
		};
		&:hover {
			cursor: pointer;
		};
	`}
`;

